import React from "react";
import { NavLink } from "react-router-dom";

//https://www.fontawesomecheatsheet.com/
//font awesome icons list

function ShopMenu(props) {

    return (
        <ul className="nav navbar-nav">

            <li><NavLink
                className="nav-link"
                to="/account"
                exact
            ><i className="fa fa-user"></i> Account</NavLink></li>

            <li><NavLink
                className="nav-link"
                to="/cart"
                exact
            ><i className="fa fa-star"></i> Wishlist</NavLink></li>

            <li><NavLink
                className="nav-link"
                to="/checkout"
                exact
            ><i className="fa fa-crosshairs"></i> Checkout</NavLink></li>

            <li><NavLink
                className="nav-link"
                to="/cart"
                exact
            ><i className="fa fa-shopping-cart"></i> Cart</NavLink></li>

            <li><NavLink
                className="nav-link"
                to="/login"
                exact
            ><i className="fa fa-lock"></i> Login</NavLink></li> 

        </ul>
    );
}
export default ShopMenu;