const mapStoreDataToProps = (storeData) => {
	return {
		selectedCode: storeData.currency,
		checkoutItems: storeData.checkout,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		updateItem: (p) => dispatch(CartActions.updateItem(p)),
		removeItem: (p) => dispatch(CheckoutActions.removeItem(p)),
	};
};

export default connect(mapStoreDataToProps, mapDispatchToProps)(Checkout);




